export const morita01 = `<div>The term <i>shoga ittai</i> (calligraphy and painting as one) suggests that there once used to be a close connection between calligraphy and painting in East Asia. However, calligraphy found itself in an awkward position when the new institutional framework of art was established during the Meiji period (1868-1912). Epitomized by Koyama Shtars assertion, calligraphy is not art, calligraphy and painting since then have been considered to be separate disciplines. Moreover, it seems the relationship between these practices was no longer questioned at all. This is because painting does not require written texts anymore, and despite the unprecedented popularity of calligraphy as a part of school education, the so-called world of calligraphy has grown increasingly reclusive, isolating itself into a kind of insular, protected community. Therefore, it is remarkable that calligraphy and painting miraculously approached each other, though for a brief moment, about half a century ago. Calligraphers were highly conscious of the possibilities of painting, and in return, painters learned from calligraphy.<br /><br />The central figure in such an interaction was the calligrapher Morita Shiry, whose uvre towers among postwar art. Yet such a simple understanding of Morita overlooks the far-reaching scope and breadth of his work. If we were to examine Moritas calligraphy from the 1960s and beyond, his relationship with Zen philosophy becomes highly significant, just as one would have to extend the analysis to the calligraphic works of ancient calligraphy masters. In the context of modern art, however, calligraphy played a critical role in the art world for about a decade in the 1950s, when Morita launched <i>Bokubi</i> (Beauty of Ink), a comprehensive journal of calligraphic art. Moritas calligraphy during this period represents the pinnacle of his career in its intensity and scale. The issues I will discuss here constitute only a part of Moritas calligraphy and theories, but I am certain that the following analysis offers insights into the great achievements of avant-garde calligraphy.<br/><br/></div>`

export const morita02 = `<div>Morita Shiry (real name Morita Kiyoshi) was born in Toyooka, Hyogo, in 1912. Morita was from a family of average farmers but he was blessed with supportive mentors. On recommendation of his primary school teacher Yoshikawa Isamu, who recognized his talent, Morita attended high school, which at the time was only accessible to sons of families of considerable wealth. Morita later recalled that it was Yoshikawa who introduced him to an anecdote about the Edo-period scholar Arai Hakuseki from which Morita adopted his art name Shiry.<a data-tool-tip='Morita Shiry, Sho, Writing and Thinking over the Last Six Decades [Sho, kaite kangaete 60-nen] in the exhibition catalog "Morita Shiry and Bokubi", Hyogo Prefectural Modern Museum of Art, 1992, p. 4.'><sup>1</sup></a> After graduating from high school, the schools principal arranged for Morita to work at the school for some time as a secretary. In 1936, he started working at the Third Kobe High School (currently Hyogo Prefectural Nagata High School) in Kobe. Moritas transfer to a new workplace was probably due to Kond Hideya, a well-respected principal of Third Kobe High School and a former principal of Toyooka High School. In his later years, Morita reflected on his good fortune and expressed gratitude for meeting such great mentors in his formative years.</div>`

export const morita02a = `<div>He also had a life-changing encounter in Kobehe met the calligrapher Ueda Sky who was visiting Kobe during a lecture tour. On Uedas recommendation, Morita moved to Tokyo and studied calligraphy under him. Like Morita, Ueda was born in the Tajima area, the northern part of Hyogo prefecture, and had studied under Hidai Tenrai. By 1938, Ueda established Shod Geijutsu Sha (Calligraphy Art Company) with Kaneko tei and others, and built a reputation as one of the pioneering avant-garde calligraphers. It did not take long for Morita to be recognized, either. As early as 1937, a year after Morita moved to Tokyo, he exhibited at the Second Great Japan Calligraphy Society Exhibition, where he received the gold commendation award for his <i>rinsho</i> (free-hand copy) of Kanjki, and won the first place among the silver awards for his original calligraphic work of a classical Chinese poem. Both works were designated as exemplary models for the Great Japan Calligraphy Society on Hidai Tenrais recommendation. He further received awards at the Japan-Manchuria-China Calligraphy Exhibition and the Third Great Japan Calligraphy Society Exhibition, garnering him recognition as a promising calligrapher while still in his twenties.<br /><br />However, Morita fell ill during this period, presumably in 1936, as it has been mentioned that this episode had happened shortly after the February 26 Incident. Morita underwent an abdominal operation followed by another due to peritoneal adhesion. For a month before and after the operations, he suffered from high temperature and unbearable pain. For Morita, however, this hardship was a sign from heaven. Writhing in his hospital bed and thinking of ways to escape from pain, he eventually ceased to solely focus on his ailing, realizing that the pain is himself, and that he had to become one with it. He remembered: We will be constrained by the brush if we perceive it as something on the exterior, conscious only of its usage and movements. Yet when we internalize the brush and become one with it, it will move along with the force of our life commands. In other words, I came to realize that the brush moves smoothest when I simply forget about its presence.<a data-tool-tip='Morita Shiry, Sho, Writing and Thinking over the Last Six Decades [Sho, kaite kangaete 60-nen] in the exhibition catalog "Morita Shiry and Bokubi", Hyogo Prefectural Modern Museum of Art, 1992, p. 7.'><sup>2</sup></a> This remark closely relates to the essence of Moritas calligraphy, especially in its divergence from the so-called automatism, and I will return to this point later.</div>`

export const morita02_short = `<div>Morita Shiry (real name Morita Kiyoshi) was born in Toyooka, Hyogo, in 1912. Morita was from a family of average farmers but he was blessed with supportive mentors. On recommendation of his primary school teacher Yoshikawa Isamu, who recognized his talent, Morita attended high school, which at the time was only accessible to sons of families of considerable wealth. Morita later recalled that it was Yoshikawa who introduced him to an anecdote about the Edo-period scholar Arai Hakuseki from which Morita adopted his art name Shiry.<a href="endnotes"><sup>1</sup></a> After graduating from high school, the schools principal arranged for Morita to work at the school for some time as a secretary. In 1936, he started working at the Third Kobe High School (currently Hyogo Prefectural Nagata High School) in Kobe. Moritas transfer to a new workplace was probably due to Kond Hideya, a well-respected principal of Third Kobe High School and a former principal of Toyooka High School. In his later years, Morita reflected on his good fortune and expressed gratitude for meeting such great mentors in his formative years.</div>`

export const morita03 = `<div>While in Tokyo, Morita edited <i>Kenpitsu</i> (Wielding the Brush), a calligraphy magazine for students, and <i>Shod Geijutsu</i> (Art of Calligraphy), which was then a major magazine for new trends in calligraphy. Interestingly, practitioners of calligraphy at the time perceived journals as a primary medium for presenting their works, more so than exhibitions and private schools. This attitude among the practitioners is telling of the distinctiveness of the discipline, and it also had a significant impact on Moritas career as a calligrapher. As the war intensified, Morita was forced to evacuate to his hometown in Toyooka at the end of 1944, and Ueda also evacuated to his hometown around the same time. Probably due to these circumstances, Morita started editing and publishing <i>Sho no Bi</i> (Beauty of Sho), a journal of Ueda Skys school of calligraphy, in 1948. Morita recalls this time as follows: </div>`

export const morita04 = `<div><i>"Although I was in the remote, northern end of Hyogo prefecture, I was boasting that this was the center of Japan. I produced calligraphic pieces for the journals, rinsho exemplars, and works for exhibitions, if there were any. I also wrote essays if necessary and enjoyed working not only as an editor but taking charge of everything. At the time, the world of avant-garde calligraphy was not fully developed yet, and it lacked any strong theoretical foundation. Seeing calligraphy in such a state was worrying me as an editor, and I gradually developed an interest in establishing a theory of calligraphy." </i><a data-tool-tip='Morita Shiry, Sho, Writing and Thinking over the Last Six Decades [Sho, kaite kangaete 60-nen] in the exhibition catalog "Morita Shiry and Bokubi", Hyogo Prefectural Modern Museum of Art, 1992, p. 7.'><sup>3</sup></a></div>`

export const morita05 = `<div>There are two points worth pointing out in the above remark. Firstly, Morita was poignantly aware of his role as an editor at the time. It was then popular to learn calligraphy by copying exemplars in magazines and journals, and Morita was involved in several such publications as an editor. The publication of <i>Sho no Bi</i> ended in 1952 with the final issue 46, but its contents were continued by the <i>Bokujin</i> (Ink People) journal. In addition, Morita also independently launched the <i>Bokubi</i> (Beauty of Ink) journal in 1951. <i>Bokujin</i> and <i>Bokubi</i> will be discussed in detail in a later section, along with the Gutai Art Associations journal <i>Gutai</i>, but it is worthy of note that many such little journals were published by artists based in the Kansai region (Western Japan) in the 1950s, and that they exerted influence not only in Japan, but to some extent also abroad. The second interesting point of Moritas above quote is his keen realization of the need for a theory of calligraphy. This was clearly a major impetus for him to establish connections with philosophers and painters, and to launch the <i>Bokubi</i> journal.</div>`

export const morita06 = `<div>Morita moved to Kyoto in 1950. During his time in Toyooka, Morita had already made acquaintance with Ijima Tsutomu, a professor of aesthetics at Kyoto University, and had developed a fascination for the Zen philosophy of Hisamatsu Shinichi, so Kyoto was an ideal place to advance his style of calligraphy and its theories. Moritas first task was to draw on his experience as an editor to launch a completely new journal, namely <i>Bokubi</i>, a comprehensive journal of calligraphic art first published in June 1951. <i>Bokubi</i>s distinctiveness from other calligraphy journals with which Morita had previously been involved is evident in the seven objectives set out in the opening statement of its inaugural issue: Aesthetic investigation of calligraphy; examining calligraphy within the whole human life; establishing the discipline of calligraphy based on ideas of modern art; examining calligraphy within a comprehensive perspective of art and aesthetics; expanding [East Asian] calligraphy on a global scale; reevaluation of classics; enhancing the social status of calligraphy. Many of these goals are rooted in a concept of interdisciplinarity that seeks to reassess calligraphy by taking into consideration its relationship to different artistic disciplines, its regional character, and its role within society as a whole.</div>`

export const morita07 = `<div>The period before and after Morita launched the <i>Bokubi</i> journal is marked by several events that had significant impact on him. In 1951, Ueda submitted a commissioned work entitled <i>Ai</i> (<span style="font-family: serif"></span>, love) to the Nitten exhibition. This work apparently was inspired by the sight of his grandchild crawling, although its form evokes the character <i>hin</i> or <i>shina</i> (<span style="font-family: serif"></span>, refinement, goods). The perceived image, therefore, contradicts the sound that the title purports to produce. At the time, the conflict between traditionalist calligraphers and avant-garde calligraphers in the calligraphy section of Nitten was intensifying, and <i>Ai</i> ignited a controversy between these opposing factions. Although Ueda eventually left the Nitten, he at first sought to carry out reforms from within by remaining a member. On the contrary, Morita and other young calligraphers criticized the outmoded and closed nature of the calligraphic establishment, and advocated for a more thorough reform.<br/><br/>

As discussed earlier, Morita spoke with gratitude of the many mentors he met in his youth, so he must have felt deeply indebted to Ueda in developing his skills and approach to calligraphy. Nevertheless, he decided to part ways with his former teacher and pursue his own path. Morita and four like-minded calligraphersInoue Yichi, Eguchi Sgen, Sekiya Yoshimichi, and Nakamura Bokushigathered at Ryanji temple in Kyoto to form the Bokujinkai calligraphy group on January 5, 1952. This was accompanied by sending out letters to Ueda and other leading figures of the calligraphy circles that detailed the purposes of forming the group. Morita described these events later with strong words as themselves rising to action. Included in the final issue of <i>Sho no Bi</i>, for which Morita was the editor, are a farewell address of the five young calligraphers, directed at Ueda, and the latters response to it. These two letters, which reveal the strong bond between a mentor and his pupils despite expressing strong disagreements, are moving to read even today.</div>`

export const morita08 = `<div>As discussed earlier, Morita spoke with gratitude of the many mentors he met in his youth, so he must have felt deeply indebted to Ueda in developing his skills and approach to calligraphy. Nevertheless, he decided to part ways with his former teacher and pursue his own path. Morita and four like-minded calligraphersInoue Yichi, Eguchi Sgen, Sekiya Yoshimichi, and Nakamura Bokushigathered at Ryanji temple in Kyoto to form the Bokujinkai calligraphy group on January 5, 1952. This was accompanied by sending out letters to Ueda and other leading figures of the calligraphy circles that detailed the purposes of forming the group. Morita described these events later with strong words as themselves rising to action. Included in the final issue of <i>Sho no Bi</i>, for which Morita was the editor, are a farewell address of the five young calligraphers, directed at Ueda, and the latters response to it. These two letters, which reveal the strong bond between a mentor and his pupils despite expressing strong disagreements, are moving to read even today.</div>`

export const morita09 = `<div>The Bokujinkai group immediately started their activities. They launched the group journal <i>Bokujin</i> and as early as January 1954 held the first Bokujin Exhibition at Maruzen Gallery in Kyoto. As it is easy to confuse, I would like to note again that <i>Bokujin</i> was published as the Bokujinkai groups journal and was first edited by Inoue Yichi, and from 1956 by Tsuji Futoshi. As a successor of <i>Sho no Bi</i>, <i>Bokujin</i> retained its nature as a journal that selects and reviews calligraphic works from readers.<br /><br />

Of particular interest is the fact that <i>Sho no Bis</i> Alpha Column, a section featuring experimental works that do not adhere to the textual nature of calligraphy, was continued in <i>Bokujin</i>. At <i>Sho no Bi</i>, Hasegawa Sabur, a painter and friend of Moritas, was responsible for selecting and reviewing contributed works for the Alpha Column, while the editors for <i>Bokujins</i> Alpha Column included Yoshihara Jir and other prominent abstract painters from Osaka and Kobe. The <i>Bokubi</i> journal, which was launched earlier, had a strong theoretical character reflecting Moritas awareness of the issues discussed above, with many contributions from scholars and critics. The seven objectives of <i>Bokubi</i> have been previously mentioned, and indeed the inaugural issue of <i>Bokubi</i> clearly followed a course of action set out in these goals. For instance, it included a cover illustration not of a calligraphic work but an Abstract Expressionist painting by the American artist Franz Kline. It also featured a commentary on Kline by Hasegawa Sabur, and even a rsum in French at the end. There is no doubt that Morita was introduced to Kline by Hasegawa, who was fluent in foreign languages and had held solo exhibitions in the United States several times. It is uncertain whether <i>Bokubi</i> was sent overseas, but Inoue Yichi at some point remarked that the journal would soon be circulated in New York.<a  data-tool-tip='Inoue Yichi, Reading Klines Letters [Kurain shi no tegami o yonde] in "Bokujin", May 1952, p. 2.'><sup>4</sup></a><br/><br/>

Notably, Japanese calligraphy attracted increasing attention in the Unites States during this period, attested by a number of calligraphy exhibitions: the <i>Modern Calligraphy Exhibition</i> by Shod Geijutsuin at the Museum of Modern Art, New York, in 1952; and the <i>Architecture and Calligraphy of Japan Exhibition</i>, curated by Arthur Drexler and held at the same museum the following year, included works by Morita, Ueda, Uno Sesson, and Shinoda Tk. Starting with Moritas participation in Salon dOctobre in 1953, Japanese calligraphers also began presenting works in Europe through exhibitions such as the Contemporary Japanese Calligraphy: <i>Art in Sumi</i> exhibition in 1955-56 that toured Amsterdam, Basel, Paris, Hamburg, and Rome, the 1957 San Paulo Biennial, and the <i>Carnegie International Exhibition of Contemporary Paintings and Sculpture</i> in 1958.</div>`

export const morita09a = `<div>Around the same time, there were many painters of then-dominant movement of Abstract Expressionism in the United States who heavily emphasized the use of strokes, just like Franz Kline, one of whose paintings was used for the cover of the first <i>Bokubi</i>. More interestingly, Jackson Pollock, Willem de Kooning, and other leading artists in the United States in the mid-1950s seemed to compete with each other to limit the use of color in their works, resulting in an increasingly monochrome approach to abstraction. According to some anecdote, the Gutai Art Associations journal <i>Gutai</i> was found in Pollocks studio after his death. It is thrilling to think that artists in Japan, particularly those based in Kansai, were able to inspire young American artists at a time when the American continent was still far to reach. Yet there is no decisive evidence that links Japanese calligraphy to American abstract painting. In the next section, I would like to return our focus to Kansai in the 1950s, which allows for a more conclusive assessment of the relationship between calligraphy and abstract painting.</div>`

export const morita10 = `<div>The cultural sphere of Kansai, where the cities of Kyoto, Osaka, and Kobe are located within close proximity, offers a sense of diversity that differs from that of Tokyo. Some time after Japans defeat in the war, and amidst reconstruction efforts, Kansai nurtured the ground for new artistic expression. Artists across the cities of Kansai and from various disciplines shared a common ground for their artistic endeavors. Firstly, Gendai Bijutsu Kondankai (Contemporary Art Discussion Group), known as Genbi, was founded in 1952, the same year the Bokujinkai group was formed. Led by Matsumura Hiroshi, who was then an art journalist at the Asahi Shinbun newspaper, Genbis primary activity involved monthly study groups to which a varied roster of  writers, artists and critics were invited.</div>`

export const morita11 = `<div>Since its very first meeting, the topics of Genbis sessions often followed a pattern of Modern Art and , making it quite clear that Genbi was intended to promote modern art across artistic disciplines in the Kansai region. Along with meetings on Modern Art and Childrens Painting and Modern Art and Commercial Art, Morita was scheduled to lecture as early as February 13, 1953, on the topic of Modern Art and Calligraphy. A transcript of this meeting is featured in issue 12 of <i>Bokujin</i>, published in April 1953. In addition to the founding members of Bokujinkai such as Morita and Inoue Yichi, attendees included painters such as Yoshihara Jir, Tsutaka Waichi, Suda Kokuta, and Shimamoto Shz, and the sculptor Horiuchi Masakazu, indicative of their keen interest in calligraphic expression. Genbi also held five exhibitions entitled Genbi-ten (Genbi Exhibition) in different locations of the Kyoto-Kobe-Osaka area, spanning the years 1953 to 1956. In addition to paintings and sculptures, the Genbi Exhibitions featured a variety of media encompassing calligraphy, crafts, industrial design, fashion, and ikebana. <i>Bokujin</i> reported on the first Genbi Exhibition in issue 18, and in turn, served itself as a platform to promote such interdisciplinary interactions. For instance, transcriptions of important lectures and roundtable talks were published in <i>Bokujin</i>, while the Alpha Column, succeeded from the <i>Sho no Bi</i> journal, was renamed the Painting Column in 1953, with abstract painters including Yoshihara Jir, Suda Kokuta, and Tsutaka Waichi selecting the contributions. Just like Franz Klines work was featured on the cover of <i>Bokubi</i>, <i>Bokujin</i>s covers included abstract paintings by Yoshihara Jir and Shimamoto Shz.</div>`

export const morita12 = `<div>I have previously mentioned that Genbi was not only a study group but also involved itself in the planning and organization of exhibitions. These Genbi Exhibitions, held five times in museums and department stores in Kyoto, Osaka, and Kobe between 1953 and 1957, displayed works of a variety of media alongside each other, with dresses and ikebana works placed among paintings and three-dimensional objects. Resembling a kind of mixed martial arts presentation, the exhibitions deeply inspired artists, and led calligraphers to recognize the urgent need for a sense of presence that withstood the large paintings next to which they were displayed. Interestingly, many abstract painters in Kansai during this period produced monochrome works with emphasized brushstrokes that clearly leaned into the domain of calligraphic abstraction. Yoshihara Jir, who later produced calligraphy-like works with a white circle against a black background (although the intentions are completely different), was at the time creating a series of paintings characterized by incisive brushstrokes with a strong sense of materiality.<br/><br/>

Hasegawa Sabur, Nakamura Shin, Suda Kokuta, and other painters who closely interacted with calligraphers also produced works that express a striking visual affinity with calligraphy. It is easy to guess that behind such developments were the previously mentioned interdisciplinary study groups, roundtable talks, and publication of journals. But why were the painters interested in the vigorous brushwork and dynamism characteristic of calligraphy in the 1950s? In the first half of the 1950s, representational painting reflecting and commenting on current social issues, known as reportage painting, was thriving in Tokyo, while abstract painting was prevalent in Kansai. Artists in Kansai, concerned with advancing a modernist approach to art, tackled challenging question such as how to overcome Cubism and the abstract styles that followed. Although Picassos Analytic Cubism and Mondrians geometric abstraction count as the pinnacles of modernist painting, both the Cubist facets and Mondrians grids petrified the pictorial surface, resulting in an impasse for the discipline of painting. The bold brushstrokes of calligraphy, therefore, were instrumental in liberating the pictorial surface once again.</div>`

export const morita12a = `<div>As early as in 1952, issue 14 of <i>Bokubi</i> featured a discussion by Morita, Yoshihara, and Suda on the calligraphy of Nantenb, a Zen priest based in Nishinomiya, Hyogo. According to Yoshihara, Nantenbs calligraphy had the potential of overcoming the rigidness of modernist painting because it was based on the temporality of the moment when wielding the brush. This notion of temporality was thoroughly pursued not so much by Yoshihara himself but by younger artists such as his pupils Shiraga Kazuo and Shimamoto Shz. It was around this time, in late 1954, when young avant-garde artists who joined Yoshiharas cause formed the Gutai Art Association, and their early works are today enshrouded by an aura of what might be called a mythology of modern art. During this period, they carried out numerous unprecedented experiments, such as actions (painting performances), outdoor exhibitions, and even exhibitions on theater stages. Gutai action paintings in particular are today considered as a consequential departure from those of Pollock. As seen in Shiragas foot paintings and Shimamotos bottle-throwing paintings, their actions provided a critical opportunity for Gutai artists to break through the rigid pictorial surface, and introduce a new dynamism to it. I believe they found a way to achieve this through interactions with Morita and other calligraphers.</div>`

export const morita13 = `<div>We might ask in return: what possibilities did the interactions with painters open up for calligraphers? By showing calligraphy at exhibitions alongside other types of artwork, avant-garde calligraphers realized the potential of calligraphy as an art form for large public spaces, rather than as a private occupation carried out at home and in classrooms like <i>rinsho</i> (copying from exemplary works or old masters) or when submitting calligraphy for the competitions held by magazines and journals. They enlarged the size of their works to achieve a sense of physical presence that would put them on par as works of art when displayed in an art museum. It should be pointed out that pioneering works of Japanese avant-garde calligraphy by those like Teshima Ykei and Hidai Tenrai were exceptionally large. At the same time, the calligraphers established an approach called <i>shjis-sho</i>, which is usually limited to only one or two characters per composition. In conventional calligraphy, the writer would render a whole passage or sentence from a given text, so the act of reading and the relationship of one characters to the other in a sequence is of importance. However, single- or two-character compositions are linked to the act of looking, hence its potential to be read visually as an image. The emergence of this style is not only related to the issue of scale but the question of whether calligraphy should be regarded as a set of characters or an imagea problem which strikes at the heart of ink abstraction.<br/><br/>

Moreover, Morita and Inoue experimented with mixing ink and materials foreign to calligraphy, such as lacquer, pigments, and glue to create calligraphic works with a pronounced sense of materiality. As a result, the works of Morita and Inoue produced at the time easily bear comparison with abstract paintings of their contemporaries in terms of their presence and intensity. Calligraphy had at last achieved a strength that equaled that of painting. I have previously mentioned that Japanese calligraphy was widely introduced to Europe and the United States in the 1950s, and that domestic exhibitions of modern art often included a section on "ink abstraction, allowing paintings and calligraphic works to be displayed in places of equal prominence. These conditions surrounding calligraphy were a natural outcome of the interrelated nature of abstract painting and avant-garde calligraphy at that time.</div>`

export const morita14 = `<div>Since the publication of <i>Morita Shiry Catalogue Raisonn, 1952-1998</i> it has become possible to study almost his whole uvre at a glance. As mentioned earlier, Moritas calligraphy from the 1950s is unparalleled in its scale and intensity, even when compared to his peers within the context of avant-garde calligraphy. But Morita did not rest there. The reason why Morita took a new departure relates to the intrinsic nature of calligraphy as an artistic expression. In my view, avant-garde calligraphy and abstract painting circles in the 1950s were highly aware of each other, as if seeing their counterpart as their own mirror image. Abstract painters envisioned in avant-garde calligraphy new possibilities of painting to come, while avant-garde calligraphers reinforced their works by taking inspiration from abstract paintings. Despite such back and forth between the two disciplines, Morita knew that there was a difference between calligraphy and painting and that by its very nature calligraphy could achieve things that were different from the possibilities of painting. For modernist painters, action meant to deliver a blow to destroy the hitherto unassailable pictorial surface. Murakami Sabur, a Gutai member, literally broke through the pictorial surface with his own body during his <i>kami-yaburi</i> (paper-breaking) performances.<br/><br/>

Meanwhile, Abstract Expressionists in the United States sought to reinvigorate painting by more moderate means, introducing a sense of the accidental through automatism, an approach borrowed from the Surrealists. Jackson Pollock is a typical example: In his poured paintings, the all-over composition derived from Cubism and the Surrealist automatism are united exquisitely. Other examples might be the paintings by de Kooning and Kline whose strokes overwhelm the pictorial surface, with a strong reliance on the accidental. With its dynamic movements, splashes, and drips Moritas calligraphy may seem to share many similarities with these styles. However, there is a crucial difference. Although Inoue Yichis works at the time abandoned calligraphys textual nature, Morita never distanced himself from characters. If one regards calligraphy not in terms of its readability but its visuality, the textual nature of calligraphy could be a hindrance. In fact, Yoshihara advised calligraphers as follows: I have become increasingly aware that the massive restriction of calligraphy lies in its textual nature. Yet I wonder if calligraphy must adhere so rigidly to characters. Maybe this restriction needs to be sacrificed in order to achieve a deeper level of creativity, even though I think that calligraphy has already come a long way.<a data-tool-tip='Yoshihara Jir, Round-table talk Calligraphy and Abstract Painting [Sho to chsh kaiga] in "Bokubi", August 1953, p. 5.'><sup>5</sup></a> It is a statement telling of Yoshihara as a modernist, but Morita in turn thought it is instead the painters who are swayed too much by the notion of that creativity. He later described in an interview with me, The important thing is to not hinder the movement of ones inner life by thinking about shapes and forms. Ultimately, what lay expansively before us are the dynamic force of life, or the reflection of the artists rich, true self, beyond visible forms.<a data-tool-tip='Interview: On Calligraphy and Abstract Painting, interviewer Osaki Shinichir, in the exhibition catalog "Morita Shiry and Bokubi", Hyogo Prefectural Museum of Modern Art, 1992, p. 4.'><sup>6</sup></a></div>`

export const morita14a = `<div>In another essay, Morita wrote critically about Western abstract painting, including that by Kandinsky and Mondrian, and he extended his views also to Pollock: He has spread out the canvas on the floor, but his dripping brush just kept movingfrom the inside to the outside, and from the outside to the inside of the realm of the canvasregardless of the limits of its surface. His movements, which have neither a fixed nor definite place, had no choice but to scatter without restraint He cannot reflect upon himself with the canvas's surface as his place, and in turn his movements, moment by moment, fail to imbue themselves with new life. His lines and shapes, the results of his movements, do not deepen their quality either.<a data-tool-tip='Morita Shiry, "Sho to Bokush" [Calligraphy and Ink Art], Shibund Publishing, 1975, p. 59.'><sup>7</sup></a> It is a scathing statement, but one wonders what was the reason for Moritas strong belief in the superiority of calligraphy over abstract painting. It might have derived from, ironically, the very textual nature of calligraphy that Yoshihara saw as a constraint. Moritas approach was distinct from the Surrealist automatism and the actions frequently employed by Gutai artists, despite their shared common features. In contrast to the expansive lines and forms of abstract painting that could never be deepened in quality, calligraphy relies on characters as its very framework. What constitutes the framework to which Morita refers to is, on the one hand, a spatial limitation imposed by the use of characters, and on the other hand, its temporality.</div>`

export const morita14b = `<div>In Moritas single-character works from that time, for instance <i>T</i> (<span style="font-family: serif"></span>) or <i>Datsu</i> (<span style="font-family: serif"></span>), we recognize their unwavering presence as texts, but also the fact that they are embedded in temporality. One of the points that Yoshihara criticized about Mondrians paintings was that it is difficult to determine how much time was spent to produce them. To paraphrase this, we cannot discern in what order the brushstrokes of action paintings are executed, while in avant-garde calligraphy we could instantly infer the movements of brush and follow its paths. This is because, as a general rule, every character has a stroke order, and these strokes are normally written from left to right and from top to bottom. Therefore a precise order and temporality is inherent in every work, even if it only consists of a single character. Admittedly, calligraphy is constrained by its textual nature. For Morita, however, this was not a real constraint. The very understanding of calligraphys textual nature as a constraint reveals, though unexpectedly, the fact that ones own thinking is still within the constraints of shape and form. Avant-garde calligraphy is defined by characters as its framework, and its temporality, which articulates an irreversible flow of time that cannot be replicated. While a brushstroke in action painting can be painted over, a stroke in calligraphy cannot be redone or corrected. Moritas calligraphy is accomplished in a single session, and he likened this impossibility of replication to the life of a calligrapher who only lives once. </div>`

export const morita15 = `<div>Therefore, he understood the degree of completion in calligraphy as a mirror of in the calligraphers life. In a Bokujinkai workshop in 1961, Morita concluded: The beauty of calligraphy lies in the reflection of ones true self, which is actualized through the characters, composed by brush, ink, and paper.<a data-tool-tip='Morita Shiry, The Beauty of Sho [Sho no utsukushisa] in "Recollections and Notes on Morita Shiry" [Ski: Morita Shiry nto], Bokujinkai Soryusha, 2013, p. 177.'><sup>8</sup></a> This relates to the notion of the movement of ones inner life, which I touched upon earlier. Morita now shifted his focus from calligraphy as an object to the calligrapher as a subject. As if echoing this idea, the <i>Bokubi</i> journal, which devoted many pages to discussions of abstract paintings in the 1950s, started featuring articles on the writings of old masters and the calligraphy of their predecessors. This moment marked the end of a brief romance between calligraphy and abstract painting. Although Morita continued producing works rife with intensity, his approach was no longer based on the idea of advancing calligraphy through competition with other artistic disciplines or avant-garde art in the United States and Europe, as he sought at the time of <i>Bokubi</i> s inauguration. Instead, he had arrived at an essentialist and spiritualist approach. However, Moritas works from the 1960s onwards, which are often associated with Zen philosophy, are beyond the scope of our discussion and call for a different perspective.<br/><br/>

As can be seen in <i>Bokubi</i>, Morita initially sought to explore calligraphy across disciplines and beyond the boundaries of East and West. Therefore, it is interesting to note that Morita soon changed his approach and developed his theories on calligraphy while regarding the abstract painting of his time and new trends in the West in rather a negative light. Moritas ideas, essentialist and reductionist as they were, recognized the distinct qualities of calligraphy that set it apart from other media, especially abstract painting, while still being in line with those of modernism. Still, his eventual turn towards a kind of spiritualism proved incompatible with the emphasis on form and objectivity characteristic of modern art. Avant-garde calligraphy was a highly contradictory effort. In Kansai in the 1950s, calligraphy and abstract painting crossed their paths, but soon parted ways again. The traces of such a creative intersection remain vividly apparent in the pioneering artistic forms of Abstract Expressionism and Art Informel, but I do not know of any other artist who probed into the meaning of that encounter as thoroughly as Morita. Indeed, Moritas avant-garde calligraphy lay at the heart of the art in the 1950s. <br/><br/><span style='float: right'>(Osaki Shinichir, Deputy Director of Tottori Prefectural Museum)</span></div>`

