export const preview = `<div>Shibunkaku is pleased to announce <i>Morita Shiry: Bokujin</i>, an exhibition that brings into the spotlight an artist who left his distinctive mark on modern art and irrevocably changed the landscape of Japanese postwar calligraphy.<br /><br />

In the early 1950s, calls for reformation grew increasingly stronger from within the circles of calligraphy, one of the traditional arts. The emerging avant-garde movement of calligraphy, incorporating inspiration from American and European abstract painting, soon began to garner recognition on the stages of the international art world. At the center of this unprecedented endeavor connecting East and West, and fusing the traditional art of calligraphy with modern abstract painting, was the calligrapher Morita Shiry. In a balancing act of combining a time-honored art with his strong intent to innovate, Morita breathed new life into calligraphy and pushed it into the attention of the world.<br /><br />

The journal <i>Bokubi</i> (Beauty of Ink), founded by Morita in 1951 and running for about 300 issues, was a new, one-of-a-kind platform for the arts that in the 1950s and 1960s encouraged the exchange among artists regardless of genre or country of origin. The art association Bokujinkai (Ink People), established in 1952 in Kyoto by Morita and four like-minded artists to challenge the outdated conventionality of Japanese calligraphy circles and re-establish the field as a genre of modern art, grew into the most representative group of Japanese postwar avant-garde calligraphy, and in the 1960s extended its activities internationally.</div>`

export const preview2 =`<div>Questioning the essence of calligraphy, Morita built on the theoretical foundations of Iijima Tsutomu, a scholar of aesthetics of Kyoto University, and sought to connect these with his own practice and Hisamatsu Shinichi (a philosopher from the Kyoto School)s theories on Zen, declaring that <i>sho</i> (calligraphy) is the site of writing characters, which manifests the dynamic movements of our inner being. The self becomes one with the constraining factors of characters, brush and ink, and the art of calligraphy unfolds on the paper in a single movement that is unified and unique, its visible traces crystallizing the calligraphic shape and melding into the meditative realm of absolute nothingness that is characteristic of Zen philosophy. It was a whole new vision of calligraphy as an art.<br /><br />

In the first half of the 1950s, the abstract painter and Gutai leader Yoshihara Jir, who like Morita had been involved in Genbi (Contemporary Art Discussion Group), argued that the potential of calligraphy was limited by its dependency on characters, indeed calligraphy should overcome these limitations by shaking free from characters altogether. Yet for Morita, characters were not a limitation, but to the contrary it was especially the fact that calligraphy depended on them as its inherent framework that allowed it to achieve its spatial expanse and unique temporality. The reliance on characters therefore was integral to calligraphy.<br /><br />

In 1963, the year of his first international solo exhibition, Morita traveled through the United States and four European countries, where he lectured on the Appeal of a Japanese Calligrapher, gave calligraphy demonstrations, and presented a movie that showed Japans avant-garde calligraphers at work. These activities were well received among the American and European abstract expressionists. Within a short time after the war, Morita Shiry succeeded in elevating Japanese calligraphy, a traditional art from embedded in East Asian culture, to the world stage by transforming his words and body movements into artistic expression. While the current cultural climate is fundamentally different from the postwar era, we believe that in a world of rapidly changing values and conditions there is a deeper meaning in turning ones attention to this artist whose work embodies a passionate quest to grasp the more essential things in art and life.<br /><br /><br/>

The present exhibition centers on Moritas calligraphy from the 1960s, when he established himself as part of the international modern art world and was widely active at home and abroad, but also includes works as early as from the 1940s up to the 1980s and 1990s. In addition to Moritas works, this exhibition includes a documentary movie, showing scenes from the studio of the calligrapher that illustrate the artists approach of converting movements of the whole body into form, and examples from <i>Bokubi</i>, the journal that Morita founded and edited, showcasing his skills as an organizer, editor and art theorist.<br /><br />
</div>`


