export const kanzanVIPtext = `As with 1964s <i>Ry wa ry wo shiru</i>, Morita relies on aluminum flake pigment in glue medium with a transparent lacquer coating for this four-panel folding screen, <i>Kanzan</i>. A work from the same period and with the same characters, but done in ink on paper, is now in the collection of the Kyoto City Museum of Art. In contrast to the version in ink, the present <i>Kanzans</i> layer of a lacquer reinforces the traces of brush movement on the works surface. Morita tried the <i>shikkin</i> (lacquer and gold) technique for the first time in the Bokujinkai Group Exhibition of 1963.<br><br>

Often that movement of the brush that Morita demonstrates so vigorously becomes the true subject matter of his works, from the moment it reaches the paper to when the stroke ends. Its twisting and turning, and the intervals between separate characters that he abbreviates are setting the whole surface of the paper into motion. The reaction of the lacquer with the aluminum pigment makes the latter appear golden, providing the work with a striking appearance that highlights its texture and rhythm. What remains to the viewer is to read the work by following the traces of the writers brush.`

export const soVIPtext = `Under the overwhelming presence of the deep black ink, the sheer existence of any traditional order of brushstrokes is virtually obliterated, yet the sticky quality of Moritas ink ensured that inside this whirlpool of flowing liquid each of his brush movements are preserved. Thus on the surface of the paper remains a vivid impression of the artists smooth coordination of brush, hand, and the movement of his entire body.<br><br>
S refers to a deep shade of blue, but in Moritas own interpretations represents an abundant sense of being alive. When in his later years he established Soryusha (Blue Dragon Society) as a center for the study of calligraphy, he used the character s in the same meaning: as a metaphor for the artists quest to continuously innovate. All known works by Morita of the character s are single-character compositions. At the same time all of them are large works, indicating that in the artists understanding its profound significance impedes any too casual treatment.<br><br>
The present work stems from a pivotal time in Moritas career when he began to fully devote himself to the creation of large-scale, single-character compositions. The mechanization of the process of grinding the ink supplied Morita with the necessary amount of the material to work on such large surfaces. However, he would soon find out that using oversized brushes led to cracking in the drying inks top coat. This is apparent in S as well, as it is permeated by a mesh of fine cracks allow glimpses of the paper ground to shine through, especially so in the areas where the ink is applied the most generously. The artist and his materials appear to be waging a battle that speaks clearly of Moritas artistic stamina. Later on, he would continue to experiment with ink, creating variants such as neri-zumi, frozen ink or bond ink. Somehow Moritas creative energy would break free through his surface cracks like streams of glowing magma on the surface of the paper.`

export const enVIPtext = `Since before the war, Morita Shiry had been eager to rely on more than just lines and dots on a flat surface but to give his works greater depth with textures comprising highs and lows. <i>En</i> is one of the thirty-three works Morita exhibited in 1963 in his solo exhibition in New York. He enlarges a single character to a huge format, aiming to imbue it with a sense of heaviness and depth. 1963 was a year of experimentation for Morita: He began to abandon the conventional shape of characters, worked with highly diluted ink, and attempted to suppress its sheen by adding lampblack, creating an even deeper black than usual. For the present work, Morita used bond ink (ink mixed with glue) to enhance the feel of heaviness. This would at the same time prevent the ink from running and enable him to write in multiple layers. In <i>En</i>, the amount of ink used is approaching an impasto effect and a sense of volume befitting a character that literally means deep pool.`